@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes RightToLeft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes BtmToTop {
    0% {
        opacity: 0;
        transform: translateY(5rem); }
    100% {
        opacity: 1;
        transform: translateY(0px); } }

@keyframes Shaker {
    0% {
        transform: rotate(0deg); }
    25% {
        transform: rotate(5deg); }
    50% {
        transform: rotate(0deg); }
    75% {
        transform: rotate(-5deg); }
    100% {
        transform: rotate(0deg); } }
