.feedback-card {
    width: 80%;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px .4rem .4rem rgba(0, 0, 0, 0.3);
    margin: 0 auto;
    padding: 5.5rem 9rem 6.5rem 9rem;
    transform: skewX(-13deg);
    backface-visibility: hidden;
    @include respond (tab-land) {
        width: 90%;
        padding: 5.5rem 4rem 6.5rem 4rem; }
    @include respond (tab-port) {
        transform: skewX(352deg); }
    @include respond (phone) {
        transform: skewX(0deg); }
    & > * {
        transform: skewX(13deg);
        @include respond (tab-port) {
            transform: skewX(-352deg); }
        @include respond (phone) {
            transform: skewX(0deg); } }
    &__img {
        width: 15rem;
        height: 15rem;
        float: left;
        border-radius: 10rem;
        shape-outside: circle(40% at 50% 50%);
        -webkit-shape-outside: circle(40% at 50% 50%);
        margin-right: 5rem;
        overflow: hidden;
        position: relative;
        @include respond (phone) {
            float: none; } }
    &__photo {
        height: 100%;
        transform: scale(1.1);
        transition: .5s all; }
    &__name {
        text-align: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -20%);
        font-weight: 400;
        font-size: 2.4rem;
        line-height: 2.9rem;
        letter-spacing: .2rem;
        color: #FFFFFF;
        text-shadow: 0px .5rem .5rem rgba(0, 0, 0, 0.7);
        text-transform: uppercase;
        opacity: 0;
        transition: .5s all; }
    &:hover &__name {
        transform: translate(-50%, -50%);
        opacity: 1; }
    &:hover &__photo {
        transform: scale(1);
        filter: blur(4px) brightness(75%); } }

