body {
    font-family: 'Roboto', sans-serif;
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-grey;
    letter-spacing: .2rem; }

.title {
    text-transform: uppercase;
    letter-spacing: 1rem;
    font-weight: 800;
    @include respond (tab-port) {
        letter-spacing: .5rem; } }

.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        font-size: 5rem;
        line-height: 6rem;
        margin-bottom: 1rem;
        animation: leftToRight 2s ease .8s;
        backface-visibility: hidden;
        animation-fill-mode: backwards;
        @include respond (tab-land) {
            font-size: 4rem; }
        @include respond (phone) {
            font-size: 5rem; } }
    &__secondary {
        display: block;
        font-size: 4rem;
        letter-spacing: .5rem;
        font-weight: 300;
        line-height: 4.8rem;
        margin-bottom: 6.5rem;
        animation: RightToLeft 2s ease .8s;
        backface-visibility: hidden;
        animation-fill-mode: backwards;
        @include respond (tab-land) {
            font-size: 3rem;
            margin-bottom: 3.5rem; }
        @include respond (tab-port) {
            letter-spacing: .3rem; }
        @include respond (phone) {
            font-size: 4.5rem; } } }

.section-about {
    &__text {
        line-height: 2.2rem;
        margin-bottom: 3rem; } }

.title-secondary {
    display: inline-block;
    text-align: center;
    font-size: 3.6rem;
    background: $color-gradient-brown-white;
    -webkit-background-clip: text;
    color: transparent;
    transition: all 1s;
    backface-visibility: hidden;
    @include respond (phone) {
        font-size: 4.2rem; }
    &_animated {
        &:hover {
            transform-origin: 50% 20%;
            transform: perspective(77.5rem) rotateY(-25deg);
            text-shadow: 0rem .4rem .4rem rgba(0, 0, 0, 0.25); } } }

.title-third {
    text-transform: uppercase;
    letter-spacing: .2rem;
    font-weight: 700;
    font-size: 2rem;
    line-height: 2.4rem;
    margin-bottom: 1rem;
    @include respond (tab-port) {
        font-size: 2.5rem;
        letter-spacing: .4rem; }
    @include respond (phone) {
        font-size: 4rem;
        line-height: 4rem; } }

.paragraph {
    font-weight: 400;
    letter-spacing: .2rem;
    font-size: 1.6rem;
    @include respond (tab-land) {
        font-size: 1.8rem; }
    @include respond (tab-port) {
        font-size: 2.2rem;
        letter-spacing: .3rem; }
    @include respond (phone) {
        font-size: 3.5rem;
        line-height: 3.5rem; } }

